<template>
    <div class="">
        <div class="search-inpt-bg" :class="{fadeIn: searchFocus}"></div>
        <div class="search" :class="{focused: searchFocus, opened: showSearch, filled: searchQuery.length > 0}">
            <input-styled
                    v-model="searchQuery"
                    :remove-btn="true"
                    :current-model="searchQuery"
                    :placeholder="' '"
                    @enter="getSearch($event)"
                    @focus="focusInput()"
                    @blur="blurInput()"
                    :placeholder-input="' '"
                    :is-clear-btn="true"></input-styled>
            <button class="btn" v-if="searchQuery.length > 0" @click="getSearch($event)"></button>
        </div>
    </div>
</template>

<script>

    import { mapState } from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue';

    export default {
        name: "SearchTop",
        data(){
            return {
                focus: false,
                searchFocus: false,
            }
        },
        props: {
                showSearch: {
                    default: function(){
                        return false
                    }
                }
        },
        components: {
                InputStyled,
        },
        computed: {
            searchQuery: {
                get () {
                    return this.$store.state.search.query
                },
                set (value) {
                    this.$store.commit('search/updateQuery', value)
                }
            },
            ...mapState({
                currentCity:  state => state.cities.current,
            }),
        },
        methods: {
            focusInput(){
                this.searchFocus = true;
                document.getElementsByTagName('body')[0].classList.add("nonscroll");
            },
            blurInput(){
                this.searchFocus = false;
                document.getElementsByTagName('body')[0].classList.remove("nonscroll");
            },
            getSearch (event) {
                this.blurInput();
                this.searchFocus = false;
                event.target.blur();
                if(this.$store.state.search.query.length > 0) {
                    if(this.$route.name !== 'search'){
                        this.$router.push('/search');
                    }
                    this.$store.dispatch('actions/initialSearchState');
                    this.$store.dispatch('actions/getCouponList', {currentSection: false, isSearch: true})
                }
            },
            resetSearch() {
                this.searchQuery = '';
                this.$refs.searchInput.focus();
            },
        }
    }
</script>

